import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { Configuration } from './configuration';
import { HttpClient } from '@angular/common/http';

import { AC03Service } from './api/ac03.service';
import { BOSISService } from './api/bosis.service';
import { CachesService } from './api/caches.service';
import { ChangesService } from './api/changes.service';
import { DispoService } from './api/dispo.service';
import { ExternalService } from './api/external.service';
import { HelpdeskService } from './api/helpdesk.service';
import { OrdersService } from './api/orders.service';
import { ProblemService } from './api/problem.service';
import { TrainVisitService } from './api/train-visit.service';
import { TroubleshootingService } from './api/troubleshooting.service';

@NgModule({
  imports:      [],
  declarations: [],
  exports:      [],
  providers: []
})
export class ApiModule {
    public static forRoot(configurationFactory: () => Configuration): ModuleWithProviders<ApiModule> {
        return {
            ngModule: ApiModule,
            providers: [ { provide: Configuration, useFactory: configurationFactory } ]
        };
    }

    constructor( @Optional() @SkipSelf() parentModule: ApiModule,
                 @Optional() http: HttpClient) {
        if (parentModule) {
            throw new Error('ApiModule is already loaded. Import in your base AppModule only.');
        }
        if (!http) {
            throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
            'See also https://github.com/angular/angular/issues/20575');
        }
    }
}
